import uniIcon from '@/components/uni-icon/uni-icon.vue';
import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue';
export default {
  data() {
    return {
      detailInfo:" ",
      specClass: 'none',
      specSelected:[]
    };
  },
  onLoad(e) {
    this.fetchData(e);
  },
  methods:{
    fetchData(e){
      var url = "goods/detail";
      var data = {
        id:e.id
      }  
      this.$Request.get(url,data).then( res => {
        if(res && res.code == 200 && res.data){
          this.detailInfo = res.data.goodsInfo;
          console.log(res);
        }
      })
    },
    //规格弹窗开关
    toggleSpec() {
    	if(this.specClass === 'show'){
    		this.specClass = 'hide';
    		setTimeout(() => {
    			this.specClass = 'none';
    		}, 250);
    	}else if(this.specClass === 'none'){
    		this.specClass = 'show';
    	}
    },
    //选择规格
    selectSpec(childIndex,childId,index){
       
     let list = this.detailInfo.spec_data[index].child;

      list.forEach( item => {
        if(item.id === childId){
          this.$set(item,'selected',true);
        }else if(item.id !== childId){
           this.$set(item,'selected',false);
        }
      })
     
      this.specSelected.forEach(item=>{
      	if(item.id === childId){
      		this.$set(item, 'selected', false);
          var seleIndex = this.specSelected.indexOf(item);
          this.specSelected.splice(seleIndex,1);
      	}
      })   
      
       // this.specSelected = [];
              
      this.detailInfo.spec_data[index].forEach(item=>{ 
      	if(item.child.selected === true){ 
      		this.specSelected.push(item.child); 
      	} 
      }) 
    },
    stopPrevent(){}
  },
  components:{
    uniIcon,
    uniNumberBox
  }
}
